@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;margin:0;padding:0}html,body{color:#fff;font-family:Roboto, sans-serif;margin:0;padding:0;text-align:center}html{background:url(/bergc-portfolio/static/media/hero.7a3b509d.jpg) no-repeat 50% 50% fixed;background-size:cover}body{-webkit-animation:fadeInAnimation ease 3s;animation:fadeInAnimation ease 3s;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes fadeInAnimation{0%{opacity:0}100%{opacity:1}}@keyframes fadeInAnimation{0%{opacity:0}100%{opacity:1}}.wrapper{display:flex;flex-direction:column;margin:0 auto;max-width:85rem;min-height:100vh;justify-content:space-between}.wrapper__content{display:flex;flex-direction:column;margin:0 auto;max-width:35rem}@media (max-width: 600px){.wrapper__content{margin:0 1rem}}.about__text{color:grey;letter-spacing:1px;line-height:1.4rem;margin-top:1rem;text-align:justify}.about__text:first-child{margin-top:0}.bighero{margin-bottom:1rem}@-webkit-keyframes show_hero{0%{opacity:0}25%{opacity:0}100%{opacity:1}}@keyframes show_hero{0%{opacity:0}25%{opacity:0}100%{opacity:1}}.bighero__header{font-weight:lighter;text-transform:capitalize;vertical-align:bottom}.bighero__button{background-color:#121212;border:1px solid #17a758;color:white;cursor:pointer;font-size:1rem;letter-spacing:0.5rem;outline:#121212;padding:0.8rem 4rem;text-transform:uppercase;transition:all 0.5s linear}.bighero__button:hover{background-color:#17a758}.contact{margin:2rem 0}.contact__list{color:grey;display:flex;list-style:none;justify-content:space-evenly}.contact__list-item{font-size:1.8rem;transition:all 0.3s ease-in-out}.contact__list-item:hover{cursor:pointer;color:white}.contact__list-item a{color:grey;text-decoration:none;transition:all 0.3s ease-in-out}.contact__list-item a:hover{color:white}.nav{font-size:1.1rem;font-weight:bold;letter-spacing:2px;margin:2rem 0;text-transform:uppercase}.nav__list{display:flex;list-style:none;justify-content:space-around}.nav__list-item{color:grey;transition:all 0.3s ease-out}.nav__list-item:hover{color:white;cursor:pointer}.nav__list-item a{color:grey;text-decoration:none;transition:all 0.3s ease-out}.nav__list-item a:hover{color:white}.portfolio__project{margin:2rem 1rem}.portfolio__project-num{font-size:0.8rem;font-weight:lighter;margin-right:0.2rem}.portfolio__project-title{color:grey;font-weight:lighter;letter-spacing:1px;text-decoration:none;text-transform:uppercase;transition:all 0.2s ease-in-out}.portfolio__project-title:hover{color:#17a758;cursor:pointer;letter-spacing:2px}.portfolio__project-title--open{color:#17a758;font-weight:lighter;letter-spacing:2px;text-decoration:none;text-transform:uppercase}.portfolio__project-title--open:hover{cursor:pointer}.portfolio__project-detail-wrapper{font-weight:lighter;margin:0 auto;max-width:45rem}.portfolio__project-detail-wrapper button{background-color:#121212;border:1px solid #17a758;cursor:pointer;font-size:1rem;letter-spacing:0.5rem;margin-bottom:1rem;outline:#121212;padding:0.8rem 4rem;text-transform:uppercase;transition:all 0.5s linear}.portfolio__project-detail-wrapper button a{color:white;text-decoration:none}.bighero__button{background-color:#121212;border:1px solid #17a758;color:white;cursor:pointer;font-size:1rem;letter-spacing:0.5rem;outline:#121212;padding:0.8rem 4rem;text-transform:uppercase;transition:all 0.5s linear}.bighero__button:hover{background-color:#17a758}.portfolio__project-detail{display:flex;justify-content:space-between;margin:1rem 0}@media (max-width: 700px){.portfolio__project-detail{flex-direction:column}}.portfolio__project-detail h4{font-weight:lighter;letter-spacing:1px;text-transform:uppercase}@media (max-width: 700px){.portfolio__project-detail h4{margin-bottom:0.5rem;text-decoration:underline}}.portfolio__project-detail p{flex-basis:70%;letter-spacing:1.5px;line-height:1.5em;text-align:justify}

